export class Button extends HTMLElement {
	connectedCallback() {
		const userClass = this.getAttribute('class') || '';
		const href = this.getAttribute('href');
		const asChild = this.hasAttribute('asChild');

		let element;

		if (asChild && this.firstElementChild) {
			// Use the first child element if asChild is true
			element = this.firstElementChild;
			// Remove it from the custom element to avoid duplication
			this.removeChild(element);
		} else {
			// Create either a button or anchor element based on the presence of href
			element = document.createElement(href ? 'a' : 'button');
			element.innerHTML = this.innerHTML;
		}

		// Apply user classes directly
		element.className = `${userClass}`.trim();

		// Apply all attributes from the custom element to the element
		for (const attr of this.attributes) {
			if (attr.name !== 'class' && attr.name !== 'asChild') {
				element.setAttribute(attr.name, attr.value);
			}
		}

		// If it's an anchor, ensure it has the href attribute
		if (href && !asChild) {
			element.setAttribute('href', href);
		}

		// Replace the custom element with the newly created or modified element
		this.replaceWith(element);
	}
}
